.icon-left{
    position: absolute;
    top: 10px;
    left: 10px;
}

.icon-right{
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.season-display{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.season-position{
    /* display: inline-block; */
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.season-display.winter{
    background-color: aliceblue
}

.season-display.summer{
    background-color: orange
}

.season-display.winter i {
    color: blue
}

.season-display.summer i {
    color: red
}


.season-display.autumn{
    background-color: #963e05
}

.season-display.spring{
    background-color: #ef9dd1
}

.season-display.autumn i {
    color: #f2821c
}

.season-display.spring i {
    color: #c0cfe1
}


.ui.container {
    width: auto!important;
    margin-left: 2em!important;
    margin-right: 2em!important;
}